.card {
	&-producer {
		position: relative;
		background-color: $white;
		box-shadow: 0px 0px 40px rgba($black, 0.02);
		border-radius: 20px;
		padding: 35px;
		margin-bottom: 20px;

		@include media-breakpoint-up(md) {
			margin-bottom: 40px;
		}

		&.active {
			.card-producer__badge {
				&.active {
					opacity: 1;
					visibility: visible;
				}
			}

			.card-producer__title {
				padding-right: 75px;
			}
		}

		&.new {
			.card-producer__badge {
				&.new {
					opacity: 1;
					visibility: visible;
				}
			}
		}

		&.delete {
			.card-producer__delete-btn {
				opacity: 1;
				visibility: visible;
			}
		}

		&__badge {
			position: absolute;
			z-index: 5;
			display: inline-flex;
			opacity: 0;
			visibility: hidden;
			transition: opacity 0.3s;

			&.active {
				font-size: 14px;
				padding: 3px 10px 1px;
				border-radius: 10px;
				color: $white;
				top: 35px;
				right: 35px;
			}

			&.new {
				top: 0;
				left: 50%;
				transform: translate(-50%, -50%);
				color: $white;
				font-size: 14px;
				line-height: 1.2;
				font-family: $font-family-sans-serif-2;
				font-weight: 700;
				letter-spacing: 0.543314px;
				border-radius: 6px;
				padding: 5px 7px;
			}
		}

		&__delete-btn {
			@include sizeElement(28px);
			@include inlineFlexCenter;

			position: absolute;
			top: 0;
			right: 0;
			transform: translate(50%, -50%);
			background-color: $gray-950;
			border-radius: 50%;
			color: $white;
			font-size: 12px;
			transition: background-color 0.3s;
			opacity: 0;
			visibility: hidden;

			&:hover {
				text-decoration: none;
				color: $white;
				background-color: lighten($gray-950, 10%);
			}
		}

		&__title {
			margin-bottom: 40px;
			color: $gray-500;
		}

		&__sub-title {
			font-size: 14px;
			margin-bottom: 40px;
			color: $gray-500;
		}

		&__info-list {
			@include listReset;

			font-size: 15px;
		}

		&__info-list-item {
			&:not(:last-child) {
				margin-bottom: 20px;
			}
		}

		&__btn-block {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			padding-top: 40px;
		}

		&__btn {
			flex-grow: 1;
			border-radius: 8px;
			min-height: 47px !important;
		}

		&__btn-icon {
			margin-right: 12px;
			margin-top: -1px;
		}

		&__cancel-chat-btn {
			@include sizeElement(30px);
			@include inlineFlexCenter;

			font-size: 26px;
			color: rgba($secondary, 0.41);
			flex-shrink: 0;
			margin: 0 5px 0 15px;
			transition: color 0.3s;

			&:hover {
				color: $blue-dark;
				text-decoration: none;
			}
		}
	}
}