.switcher-item {
	display: inline-flex;
	flex-shrink: 0;
	cursor: pointer;

	&__input {
		@include visuallyHidden;

		&:checked {
			+ .switcher-item__block-wrap {
				.switcher-item__block {
					&:after {
						left: calc(100% - 1px);
						transform: translate(-100%, -50%);
					}
				}
			}
		}

		&:not(:checked) {
			+ .switcher-item__block-wrap {
				.switcher-item__block {
					background-color: rgba($secondary, 0.08) !important;
				}
			}
		}
	}

	&__block-wrap {
		display: inline-flex;
		align-items: center;

		@include media-breakpoint-up(xs) {
			flex-direction: row-reverse;
		}

		@include media-breakpoint-up(lg) {
			flex-direction: row
		}
	}

	&__block {
		position: relative;
		width: 37px;
		height: 22px;
		border-radius: 22px;
		transition: background-color 0.3s;

		&:after {
			@include sizeElement(20px);

			content: '';
			position: absolute;
			top: 50%;
			left: 1px;
			transform: translate(0, -50%);
			z-index: 5;
			display: block;
			border-radius: 50%;
			background-color: $white;
			box-shadow: 0px 2.17647px 5.80392px rgba($black, 0.15);
			transition: left 0.3s, transform 0.3s;
		}
	}

	&__text {
		font-family: $font-family-sans-serif-2;
		font-weight: 500;
		padding-left: 14px;
		padding-top: 1px;
		color: $gray-950;
		font-size: 14px;
		letter-spacing: -0.5px;

		@include media-breakpoint-up(xs) {
			padding-right: 14px;
			padding-left: 0;
		}

		@include media-breakpoint-up(lg) {
			padding-left: 10px;
			padding-right: 0;
			font-size: 16px;
			padding-top: 3px;
		}
	}
}

.checkbox-filter-item {
	display: inline-flex;
	cursor: pointer;

	&:hover {
		.checkbox-filter-item__text {
			border-color: $gray-950;
		}
	}

	&__input {
		@include visuallyHidden;

		&:not(:checked) {
			+ .checkbox-filter-item__text {
				background-color: $white !important;
			}
		}

		&:checked {
			+ .checkbox-filter-item__text {
				border-color: transparent;
				color: $white;
			}
		}
	}

	&__text {
		font-family: $font-family-sans-serif-2;
		font-weight: 500;
		color: $gray-950;
		font-size: 16px;
		line-height: 1;
		letter-spacing: 0.1px;
		border: 1px solid $gray-300;
		background-color: $white;
		border-radius: 22px;
		padding: 3px 10px 1px;
		transition: background-color 0.3s, border-color 0.3s, color 0.3s;
	}
}