.notification-block {
	position: relative;
	padding: 12px 60px 12px 20px;
	color: $white;
	border-radius: 10px;
	margin: 15px 0 25px;

	@include media-breakpoint-up(lg) {
		padding: 25px 100px 25px 30px;
		margin: 20px 0 40px;
		border-radius: 14px;
	}

	&__close-btn {
		@include inlineFlexCenter;
		@include sizeElement(20px);

		position: absolute;
		top: 50%;
		right: 20px;
		border-radius: 50%;
		color: $secondary;
		font-size: 8px;
		transform: translate(0, -50%);
		transition: transform 0.3s;

		@include media-breakpoint-up(lg) {
			@include sizeElement(28px);

			font-size: 12px;
			right: 25px;
		}

		&:hover {
			text-decoration: none;
			transform: translate(0, -50%) rotate(90deg);
		}
	}

	&__text {
		font-size: 12px;
		margin-bottom: 0;

		@include media-breakpoint-up(lg) {
			display: flex;
			font-size: 15px;
		}
	}

	&__title {
		white-space: nowrap;

		@include media-breakpoint-up(lg) {
			font-size: 14px;
		}

		&:after {
			content: '.';
			display: inline;

			@include media-breakpoint-up(lg) {
				content: none;
			}
		}
	}
}