.dropdown {
	&-navigation {
		display: inline-flex;

		&__toggler {
			position: relative;
			display: inline-flex;
			padding-left: 25px;

			@include media-breakpoint-up(lg) {
				padding-left: 35px;
			}

			&:before {
				content: "\e906";
				position: absolute;
				top: 50%;
				left: 0;
				transform: translate(0, -50%);
				font-family: icomoon;
				display: inline-block;
				font-size: 6px;
				color: $blue-dark;
				transition: transform 0.3s;

				@include media-breakpoint-up(lg) {
					font-size: 8px;
				}
			}

			&[aria-expanded="true"] {
				&:before {
					transform: translate(0, -50%) rotate(180deg);
				}
			}

			&:hover {
				.dropdown-navigation__photo-img {
					&[viewBox] {
						fill-opacity: 0.8;
					}

					&:not([viewBox]) {
						opacity: 0.8;
					}
				}
			}
		}

		&__toggler-photo {
			@include sizeElement(33px);

			position: relative;
			overflow: hidden;
			border-radius: 50%;
			transition: opacity 0.3s;

			@include media-breakpoint-up(lg) {
				@include sizeElement(45px);
			}
		}

		&__photo-img {
			&[viewBox] {
				transition: fill-opacity 0.3s;
			}

			&:not([viewBox]) {
				transition: opacity 0.3s;
			}
		}

		&__dropdown-menu {
			min-width: 200px;
			width: 200px;
			border: 1px solid rgba($white, 0.3);
			border-radius: 0;
			padding: 0;
			margin-top: 8px !important;
			margin-right: -1px !important;

			@include media-breakpoint-up(lg) {
				min-width: 230px;
				width: 230px;
			}
		}

		&__dropdown-menu-item {
			&:not(:last-child) {
				.dropdown-navigation__dropdown-menu-link {
					border-bottom: 1px solid rgba($white, 0.3);
				}
			}

			&:first-child {
				.dropdown-navigation__dropdown-menu-link {
					border-radius: 8px 8px 0 0;
				}
			}

			&:last-child {
				.dropdown-navigation__dropdown-menu-link {
					border-radius: 0 0 8px 8px;
				}
			}
		}

		&__dropdown-menu-link {
			position: relative;
			display: block;
			color: $white;
			font-size: 14px;
			padding: 8px 13px 8px 40px;
			font-family: $font-family-sans-serif-2;
			transition: opacity 0.3s;

			@include media-breakpoint-up(lg) {
				font-size: 16px;
			}

			&:hover {
				text-decoration: none;
				color: $white;
				opacity: 0.5;
			}

			&.revert {
				padding-left: 13px;
				padding-right: 40px;

				.dropdown-navigation__dropdown-menu-link-icon {
					left: auto;
					right: 13px;
				}
			}
		}

		&__dropdown-menu-link-icon {
			position: absolute;
			top: 50%;
			left: 13px;
			transform: translate(0, -50%);
			z-index: 5;
			font-size: 15px;
		}
	}
}