.filter-block {
	position: relative;
	margin-bottom: 15px;

	@include media-breakpoint-up(sm) {
		margin-bottom: 30px;
	}

	@include media-breakpoint-up(lg) {
		margin-bottom: 50px;
	}

	&.active {
		[class^="col"] {
			&:nth-child(1) {
				.filter-block__dropdown-toggle {
					border-bottom-left-radius: 0;
				}
			}

			&:nth-child(2) {
				.filter-block__dropdown-toggle {
					border-bottom-right-radius: 0;
				}
			}
		}
	}

	&__overlay {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($secondary, 0.1);
		opacity: 0;
		visibility: hidden;
		z-index: $zindex-search-filter-overlay;
		transition: opacity 0.3s, visibility 0.3s;

		&.active {
			opacity: 1;
			visibility: visible;
		}
	}

	&__dropdown {
		position: static;
	}

	[class^="col"] {
		&:nth-child(1) {
			.filter-block__dropdown-toggle {
				border-top-left-radius: 14px;
				border-bottom-left-radius: 14px;
			}
		}

		&:nth-child(2) {
			.filter-block__dropdown-toggle {
				border-top-right-radius: 14px;
				border-bottom-right-radius: 14px;
				border-left: 2px solid $gray-400;
			}
		}
	}

	&__dropdown-toggle {
		position: relative;
		display: flex;
		align-items: center;
		width: 100%;
		border: none;
		background-color: $white;
		box-shadow: 9px 32px 35px rgba($black, 0.0464653);
		font-family: $font-family-sans-serif-2;
		font-weight: 600;
		font-size: 12px;
		line-height: 1.14;
		height: 60px;
		padding: 10px 30px 10px 15px;
		text-align: left;
		color: $gray-950;
		transition: background-color 0.3s;

		@include media-breakpoint-up(xs) {
			padding: 10px 35px 10px 20px;
		}

		@include media-breakpoint-up(lg) {
			font-size: 16px;
			height: 84px;
			padding: 15px 60px 15px 30px;
		}

		&:hover {
			background-color: darken($white, 2%);
		}

		&:after {
			content: "\e906";
			position: absolute;
			top: 50%;
			right: 15px;
			transform: translate(0, -50%);
			display: inline-block;
			font-family: icomoon;
			font-size: 6px;
			transition: transform 0.3s;

			@include media-breakpoint-up(xs) {
				right: 20px;
			}

			@include media-breakpoint-up(lg) {
				right: 35px;
				font-size: 7px;
			}
		}

		&.show {
			z-index: $zindex-search-filter-dropdown;

			+ .filter-block__dropdown-menu {
				z-index: $zindex-search-filter-dropdown;
			}

			&:after {
				transform: translate(0, -50%) rotate(180deg);
			}
		}
	}

	&__dropdown-filter-counter {
		@include sizeElement(22px);
		@include inlineFlexCenter;

		color: $white;
		padding: 2px;
		font-size: 11px;
		font-weight: 600;
		border-radius: 22px;
		margin-right: 8px;
		flex-shrink: 0;

		@include media-breakpoint-up(lg) {
			font-size: 13px;
			min-width: 29px;
			width: auto;
			height: auto;
			padding: 3px 10px 1px;
		}
	}

	&__dropdown-menu {
		width: 100%;
		min-width: auto;
		border: none;
		border-top: 2px solid $gray-400;
		padding: 0;
		margin: 0 !important;
		border-radius: 0 0 14px 14px;
		box-shadow: 9px 32px 35px rgba(0, 0, 0, 0.0464653);
	}

	&__dropdown-search-bar {
		@include media-breakpoint-up(xs) {
			display: flex;
			align-items: center;
			padding-right: 20px;
		}

		@include media-breakpoint-up(lg) {
			padding-right: 35px;
		}
	}

	&__search-input {
		@include placeholder {
			color: rgba($gray-950, 0.5);
		};

		display: block;
		width: 100%;
		border: none;
		border-radius: 0;
		height: 56px;
		padding: 15px 15px 15px 40px;
		background-color: $white;
		font-family: $font-family-sans-serif-2;
		font-weight: 500;
		color: $gray-950;
		background-image: url("../img/search-icon.svg");
		background-repeat: no-repeat;
		background-position: left 20px center;
		background-size: 12px;
		font-size: 14px;

		@include media-breakpoint-up(lg) {
			font-size: 16px;
			padding: 15px 30px 15px 50px;
			background-position: left 30px center;
		}
	}

	&__switcher-item {
		margin-left: 15px;

		@include media-breakpoint-up(xs) {
			margin-left: 0;
		}
	}

	&__dropdown-filter-block {
		padding: 15px;

		@include media-breakpoint-up(xs) {
			padding: 10px 20px 20px;
		}

		@include media-breakpoint-up(lg) {
			border-top: 2px solid $gray-400;
			padding: 20px 15px 20px 35px;
		}
	}

	&__dropdown-filters-list {
		@include listReset;
		@include customScrollbar(8px, $white, $gray-500, 8px);

		display: flex;
		flex-wrap: wrap;
		max-height: 215px;
		overflow: auto;
	}

	&__dropdown-filter-list-item {
		padding-right: 8px;
		padding-bottom: 5px;

		@include media-breakpoint-up(lg) {
			padding-bottom: 14px;
		}
	}

	&__dropdown-filter-actions-list {
		@include listReset;

		display: flex;
		flex-wrap: wrap;
		justify-content: end;
		padding: 10px 0 0 0;

		@include media-breakpoint-up(lg) {
			padding: 20px 15px 0 0;
		}
	}

	&__dropdown-filter-actions-list-item {
		&:not(:first-child) {
			margin-left: 16px;
		}
	}
}