.no-invitations-block {
	background: $white;
	box-shadow: 0 0 40px rgba($black, 0.02);
	border-radius: 12px;
	padding: 56px 46px;
	text-align: center;
	max-width: 500px;
	margin: 0 auto;

	@include media-breakpoint-up(lg) {
		padding: 80px 50px 50px;
		max-width: 710px;
	}

	&__icon {
		max-width: 80px;
		margin: 0 auto 30px;

		@include media-breakpoint-up(lg) {
			max-width: 100px;
		}
	}

	&__text {
		max-width: 450px;
		margin: 0 auto;
		letter-spacing: 0.258721px;
		color: $gray-600;
	}
}