.btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;

	&:not(.btn-sm):not(.btn-lg) {
		min-height: 38px;
		letter-spacing: -0.5px;

		@include media-breakpoint-down(lg) {
			font-size: 14px;
			min-height: 35px;
		}
	}

	&-link {
		font-weight: 500;
		color: $gray-950;
	}
}

.page-nav-btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	flex-wrap: nowrap;
	font-size: 12px;
	font-family: $font-family-sans-serif-2;
	letter-spacing: -0.5px;
	min-height: 44px;
	padding: 8px 12px;
	border-radius: 6px;
	transition: color 0.3s, background-color 0.3s;

	@include media-breakpoint-up(lg) {
		font-size: 16px;
		min-height: 68px;
		padding: 16px 30px;
		border-radius: 12px;
	}

	&:hover {
		text-decoration: none;
	}

	&.like-link-on-desktop {
		@include media-breakpoint-up(lg) {
			min-height: auto;
			padding: 0;
			background-color: transparent !important;
			color: $blue-dark !important;
			border-radius: 0;
		}

		&:hover {
			@include media-breakpoint-up(lg) {
				color: lighten($blue-dark, 8%) !important;
			}
		}

		.page-nav-btn__icon {
			@include media-breakpoint-up(lg) {
				padding-right: 0.375em;
			}
		}
	}

	&.blue-dark {
		background-color: $blue-dark;
		color: $white;

		&:hover {
			background-color: lighten($blue-dark, 8%);
		}
	}

	&.gray {
		background-color: rgba($black, 0.1);
		color: $blue-dark;

		&:hover {
			background-color: rgba($black, 0.3);
		}
	}

	&.with-dot {
		.page-nav-btn__icon {
			&:after {
				@include sizeElement(8px);

				content: '';
				position: absolute;
				top: -0.2em;
				right: 0.25em;
				display: block;
				background-color: $red;
				border-radius: 50%;

				@include media-breakpoint-up(lg) {
					@include sizeElement(11px);

					top: -0.3em;
					right: 0.15em;
				}
			}
		}
	}

	&__icon {
		position: relative;
		display: inline-block;
		font-size: 1.5em;
		padding-right: 0.7em;
	}

	&__text {
		padding-top: 0.3125em;
	}
}

.link-like-icon {
	display: inline-flex;
	line-height: 1;
	font-size: 20px;
	transition: opacity 0.3s;

	&:hover {
		opacity: 0.85;
		text-decoration: none;
	}
}