body {
	min-width: 320px;

	&.orange-theme {
		background-color: $orange-faded;

		.text-primary {
			color: $orange !important;
		}

		.bg-primary {
			background-color: $orange !important;
		}

		.bg-primary-faded {
			background-color: $orange-faded !important;
		}

		.fill-primary {
			fill: $orange !important;
		}

		.btn-primary {
			background-color: $orange;
			border-color: $orange;

			&:hover {
				background-color: lighten($orange, 3%);
				border-color: lighten($orange, 3%);
			}
		}

		.btn-outline-primary {
			border-color: $orange;
			color: $orange;

			&:hover {
				background-color: $orange;
				color: $white;
			}
		}

		.btn-primary-faded {
			background-color: $orange-faded-2;
			border-color: $orange-faded-2;
			color: $orange;

			&:hover {
				background-color: darken($orange-faded-2, 3%);
				border-color: darken($orange-faded-2, 3%);
			}
		}
	}

	&.green-theme {
		background-color: $green-faded;

		.text-primary {
			color: $green !important;
		}

		.bg-primary {
			background-color: $green !important;
		}

		.bg-primary-faded {
			background-color: $green-faded !important;
		}

		.fill-primary {
			fill: $green !important;
		}

		.btn-primary {
			background-color: $green;
			border-color: $green;

			&:hover {
				background-color: lighten($green, 3%);
				border-color: lighten($green, 3%);
			}
		}

		.btn-outline-primary {
			border-color: $green;
			color: $green;

			&:hover {
				background-color: $green;
				color: $white;
			}
		}

		.btn-primary-faded {
			background-color: $green-faded-2;
			border-color: $green-faded-2;
			color: $green;

			&:hover {
				background-color: darken($green-faded-2, 3%);
				border-color: darken($green-faded-2, 3%);
			}
		}
	}
}

.main {
	overflow: hidden;
}

.container,
.container-fluid {
	@include media-breakpoint-down(sm) {
		padding-left: 16px;
		padding-right: 16px;
	}
}

.row {
	&:not(.g-0) {
		@include media-breakpoint-down(sm) {
			margin-left: -16px;
			margin-right: -16px;
		}

		.col,
		[class^="col"] {
			@include media-breakpoint-down(sm) {
				padding-left: 16px;
				padding-right: 16px;
			}
		}
	}
}