.page-tabs-links {
	@include listReset;

	text-align: center;

	@include media-breakpoint-up(md) {
		text-align: left;
	}

	&__item {
		display: inline-block;
		padding-top: 10px;
		padding-bottom: 10px;
		margin: 0 17px;

		@include media-breakpoint-up(md) {
			margin-right: 35px;
			margin-left: 0;
		}

		@include media-breakpoint-up(lg) {
			margin-right: 50px;
		}

		&:last-child {
			@include media-breakpoint-up(md) {
				margin-right: 0;
			}
		}
	}

	&__link {
		position: relative;
		display: inline-block;
		font-family: $font-family-sans-serif-2;
		font-size: 14px;
		font-weight: 700;
		color: rgba($secondary, 0.4);
		letter-spacing: 0.543314px;
		transition: color 0.3s;

		@include media-breakpoint-up(lg) {
			font-size: 20px;
		}

		&.active {
			font-size: 22px;
			color: $secondary;

			@include media-breakpoint-up(lg) {
				font-size: 42px;
			}
		}

		&.with-dot {
			&:after {
				@include sizeElement(6px);

				content: '';
				position: absolute;
				top: 0;
				right: -8px;
				z-index: 5;
				background-color: $red;
				border-radius: 50%;

				@include media-breakpoint-up(lg) {
					@include sizeElement(11px);

					top: -2px;
					right: -12px;
				}
			}
		}

		&:hover {
			text-decoration: none;
			color: $secondary;
		}

		&:not(.active) {
			&:hover {
				color: rgba($secondary, 0.6);
			}
		}
	}
}